// @flow
import React, { Component } from 'react';
import styles from './PromotionRow.css';


export default class PromotionInfo extends Component {
  constructor(props) {
    super(props);
  }

  render(){
    let createMarkup = (p) => { return {__html: p}};
    return (
        <div>
            <div>
                <h5>img: </h5>
                <div className={styles.contentDiv}><img src={(this.props.configapp.botemaniaUrl + this.props.data.image).replace("/scale-s%s/banner-r%s-w%s.jpg","/scale-1/banner-20-300.jpg")}/></div>
            </div>
            <div>
                <h5>Path: </h5>
                <div className={styles.contentDiv}>{this.props.data.path}</div>
            </div>
            {this.props.promoinfo.content && 
                <div>
                    {this.props.promoinfo.content.labels && 
                    <div>
                        <h5>Labels: </h5>
                        <div className={styles.contentDiv}  dangerouslySetInnerHTML={createMarkup(JSON.stringify(this.props.promoinfo.content.labels).split('":"').join(":</b>").split('","').join("<br/><b>").split('{"').join("<b>").split('"}').join(""))}></div>
                    </div>
                    }
                    {this.props.promoinfo.content.preScreen && 
                    <div>
                        <h5>preScreen: </h5>
                        <div className={styles.contentDiv} dangerouslySetInnerHTML={createMarkup(this.props.promoinfo.content.preScreen.copy)}></div>
                    </div>
                    }
                    {this.props.promoinfo.content.expiredScreen && 
                    <div>
                        <h5>expiredScreen: </h5>
                        <div className={styles.contentDiv} dangerouslySetInnerHTML={createMarkup(this.props.promoinfo.content.expiredScreen.copy)}></div>
                    </div>
                    }
                    {this.props.promoinfo.content.loggedoutScreen && 
                    <div>
                        <h5>loggedoutScreen: </h5>
                        <div className={styles.contentDiv} dangerouslySetInnerHTML={createMarkup(this.props.promoinfo.content.loggedoutScreen.copy)}></div>
                    </div>
                    }
                    {this.props.promoinfo.content.ineligibleScreen && 
                    <div>
                        <h5>ineligibleScreen: </h5>
                        <div className={styles.contentDiv} dangerouslySetInnerHTML={createMarkup(this.props.promoinfo.content.ineligibleScreen.copy)}></div>
                    </div>
                    }
                    {this.props.promoinfo.content.optInScreen && 
                    <div>
                        <h5>optInScreen: </h5>
                        <div className={styles.contentDiv} dangerouslySetInnerHTML={createMarkup(this.props.promoinfo.content.optInScreen.copy)}></div>
                    </div>
                    }
                    {this.props.promoinfo.content.completeScreen && this.props.promoinfo.content.completeScreen.copy &&
                    <div>
                        <h5>completeScreen: </h5>
                        <div className={styles.contentDiv} dangerouslySetInnerHTML={createMarkup(Array.isArray(this.props.promoinfo.content.completeScreen.copy)?this.props.promoinfo.content.completeScreen.copy.join():this.props.promoinfo.content.completeScreen.copy)}></div>
                    </div>
                    }
                </div>
            }
        </div>
    );
  }
}
