// @flow
import React, { Component } from 'react';
import styles from './PromotionRow.css';
import PromotionInfo from './PromotionInfo';
import * as moment from 'moment';

moment.locale('es');  

export default class PromotionRow extends Component {
  constructor(props) {
    super(props);
    this.state = {showInfo: false, promoInfo : {settings:{env:{prod:{promoID:''}}}}  , promoTerms:''  };
    this.changeShowInfo = this.changeShowInfo.bind(this);
  }

  changeShowInfo(){ 
    this.setState(state => ({
      showInfo: !state.showInfo
    }));
  };

  componentDidMount() {
    let promoBasicURL = this.props.configapp.botemaniaUrl+ this.props.data.url.replace('index.html','');
    try{
      fetch(`https://api.allorigins.win/get?url=${encodeURIComponent(promoBasicURL + "config.json")}`)
      .then(response => response.json())
      .then(data => {
        this.setState({promoInfo: JSON.parse(data.contents)})
      });
    }catch{
    }
    try{
      fetch(`https://api.allorigins.win/get?url=${encodeURIComponent(promoBasicURL + "terms.html")}`)
      .then(response => response.json())
      .then(data => {
        this.setState({promoTerms: data.contents})
      });
    }catch{
    }
    
  }


  render(){
    let createMarkup = (p) => { return {__html: p}};
    var today = moment();
    return (
      <tbody className={styles.container} data-tid="container">
        <tr  onClick={this.changeShowInfo} style={{background:this.state.showInfo?'whitesmoke':'', cursor:'pointer', borderBottom:this.state.showInfo?'':'1px solid #bfbfbf'}}>

          <td>
            {this.props.proximamente?(
             ((
                        moment(this.props.proximamente.expiryDate,"DD/MM/YYYY").isSame(moment(this.props.data.startDate,"DD/MM/YYYY"),'day')
                    &&  moment(this.props.proximamente.startDate,"DD/MM/YYYY").isSame(moment(this.props.data.startDate,"DD/MM/YYYY").subtract(5, 'days'),'day')
              )?'‚úîÔ∏è':'‚úñÔ∏è') + 'üîú'+
              ((moment(this.props.proximamente.startDate,"DD/MM/YYYY"))>today?'üîÆ':(moment(this.props.proximamente.expiryDate,"DD/MM/YYYY"))<today?'üßü‚ÄçÔ∏è':'üî•Ô∏è')
            ):''}


          </td>
          <td>
            ‚õì  Ô∏è
            {(
              (this.props.data.image=='/api/content/promotions/'+this.props.data.path+'/images/banner/scale-s%s/banner-r%s-w%s.jpg' && 
              this.props.data.url=='/api/content/promotions/'+this.props.data.path+'/index.html')
             || 
              (this.props.data.image=='/api/content/promotions/vip/'+this.props.data.path+'/images/banner/scale-s%s/banner-r%s-w%s.jpg' && 
              this.props.data.url=='/api/content/promotions/vip/'+this.props.data.path+'/index.html')
              )?'‚úîÔ∏è':'‚úñÔ∏è'}
          </td>
          <td>{this.props.data.title}</td>
          <td>{this.state.promoInfo.settings.env.prod.promoID}</td>
          <td>
            {(moment(this.props.data.startDate,"DD/MM/YYYY"))>today?'üîÆ':(moment(this.props.data.expiryDate,"DD/MM/YYYY"))<today?'üßü‚ÄçÔ∏è':'üî•Ô∏è'}
          </td>
          <td style={{color:this.props.data.startDate==this.state.promoInfo.settings.startDate?'green':'red'}}>{moment(this.props.data.startDate,"DD/MM/YYYY").format('DD MMM YY')}</td>
          <td style={{color:this.props.data.expiryDate==this.state.promoInfo.settings.expiryDate?'green':'red'}}>{moment(this.props.data.expiryDate,"DD/MM/YYYY").format('DD MMM YY')}</td>
          <td>{this.props.data.bucket}</td>          
          <td>{this.props.data.partners?this.props.data.partners.join(','):''}</td>
          <td>{this.props.data.devices?this.props.data.devices.join(','):''}</td>
        </tr>
        <tr style={{display:this.state.showInfo?'table-row':'none'}}>
          <td colSpan="10" className={styles.infoRow} >
            <div style={{width:'50%', display:'inline-block', verticalAlign:'top'}}>
              <PromotionInfo data={this.props.data} configapp={this.props.configapp} promoinfo={this.state.promoInfo}></PromotionInfo>
            </div>
            <div style={{width:'50%', display:'inline-block', verticalAlign:'top'}}>
              <h5>T√©rminos: </h5>
              <div  className={styles.contentDiv} style={{maxHeight:'500px', overflow:'auto'}} dangerouslySetInnerHTML={createMarkup(this.state.promoTerms)}></div>  
            </div>
          </td>
        </tr>
      </tbody>
    );
  }
}
