// @flow
import React, { Component } from 'react';
import styles from './Home.css';
import PromotionRow from './PromotionRow';



export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {data: true, promos:[{promotions:[]}], proximamente:[], proximamenteMap:[]};
  }

  componentDidMount() {
    let promotionsPagePath = "https://www.botemania.es/api/config/promotions/v2/promotions-page.json";
    fetch(`https://api.allorigins.win/get?url=${encodeURIComponent(promotionsPagePath)}`)
    .then(response => response.json())
    .then(data => {
      let promos = JSON.parse(data.contents).categories;
      let proximamenteMap = [];
      promos[1].promotions.map(p=>proximamenteMap[p.path]=p);
      this.setState({promos: promos, proximamente: promos[1], proximamenteMap: proximamenteMap})
    });
  }

  render() {
    let configapp = {
      promotionsPagePath: "https://www.botemania.es/api/config/promotions/v2/promotions-page.json",
      botemaniaUrl:"https://botemania.es"
    }  
    
    return (
      <div className={styles.container} data-tid="container">
        <h2>Promociones</h2>
        <div>
          <table>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th>Title</th>
                  <th>ID</th>
                  <th></th>
                  <th>Start</th>
                  <th>End</th>
                  <th>Bucket</th>
                  <th>Partner</th> 
                  <th>Devices</th>
                </tr>
              </thead>
              {this.state.promos[0].promotions.map((e, index1)=><PromotionRow configapp={configapp} data={e} proximamente={this.state.proximamenteMap[e.path]} key={index1}></PromotionRow>)}
            </table>
        </div>
      </div>
    );
  }
}


